.App {
  font-family: Arial, sans-serif;
  text-align: center;
  background-color: #191414; /* Dark background color similar to Spotify */
  color: #fff; /* White text color */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Adjust to start content at the top */
  padding: 20px;
  box-sizing: border-box; /* Ensure padding is included in width calculation */
}

.header {
  font-size: 24px;
  margin-bottom: 20px;
  color: #fff; /* Black font color */
}

a {
  display: inline-block;
  padding: 10px 20px;
  background-color: #1db954;
  color: #fff;
  text-decoration: none;
  border-radius: 4px;
  margin-bottom: 20px;
}

a:hover {
  background-color: #0f802d;
}

.total-liked-songs {
  border: 2px solid #1db954;
  background-color: #f2f2f2;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  width: 100%; /* Ensure full width */
  box-sizing: border-box; /* Ensure padding is included in width calculation */
  color: #000; /* Black font color */
}

.song-container {
  display: flex;
  justify-content: space-between; /* Ensure space between songs */
  align-items: center; /* Center items vertically */
  margin-bottom: 20px;
}

.song {
  cursor: pointer;
  text-align: center;
  width: 45%; /* Adjust width of each song container */
}

.song img {
  width: 100%; /* Ensure image fills container */
  height: auto;
  border-radius: 8px;
  margin-bottom: 10px;
}

.buttons {
  margin-top: 20px;
  display: flex;
  flex-direction: column; /* Stack buttons vertically */
  align-items: center; /* Center items horizontally */
}

.buttons-container {
  display: flex;
  flex-direction: column; /* Stack buttons vertically */
  align-items: center; /* Center items horizontally */
}

.buttons-container button {
  padding: 15px 30px; /* Larger padding for button */
  font-size: 18px; /* Larger font size for button text */
  margin: 10px 0; /* Adjust vertical margin between buttons */
  cursor: pointer;
  background-color: #1db954;
  color: #fff;
  border: none;
  border-radius: 4px;
  width: 200px; /* Increase button width */
}

.buttons-container button:hover {
  background-color: #0f802d;
}

.buttons button {
  padding: 15px 30px; /* Larger padding for button */
  font-size: 18px; /* Larger font size for button text */
  margin: 10px 0; /* Adjust vertical margin between buttons */
  cursor: pointer;
  background-color: #1db954;
  color: #fff;
  border: none;
