}

.buttons button:hover {
  background-color: #0f802d;
}


.progress-container {
  width: 100%;
  margin-bottom: 20px;
}

.progress-bar {
  background-color: #ddd;
  height: 20px;
  border-radius: 10px;
  overflow: hidden;
}

.progress {
  background-color: #1db954;
  height: 100%;
  text-align: center;
  line-height: 20px;
  color: #fff;
  width: 0; /* Start with 0 width */
}

.ranked-songs {
  margin-top: 40px;
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: #fff; /* White background for table */
  border-radius: 8px;
  overflow: hidden;
}

th,
td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;
  color: #000; /* Black font color */
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #e6f7ff;
}

